@import url(https://fonts.googleapis.com/css?family=Open+Sans+Condensed:300,300i,700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Lato:400,600,600i&display=swap);
@import url(https://fonts.googleapis.com/css?family=Ubuntu&display=swap);
@import url(https://fonts.googleapis.com/css?family=Ubuntu:300,400,500,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #eaeaea;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

*{
    box-sizing: border-box;
}
.wrap{
    width: 80vw;
    margin-left: auto;
    margin-right: auto;
}
.flex{
    display: flex;
} 
.jc-between{
justify-content: space-between;
}
.homepage{display:flex;flex-direction:column;align-items:center;padding:20px 80px}

.menu-item{min-width:30%;height:240px;flex:1 1 auto;display:flex;align-items:center;justify-content:center;border:1px solid black;margin:0 7.5px 15px;font-family:'Open Sans Condensed', sans-serif;overflow:hidden;-webkit-transition:all .3s ease;transition:all .3s ease}.menu-item:hover{cursor:pointer}.menu-item:hover .background-image{-webkit-transform:scale(1.1);transform:scale(1.1);-webkit-transition:all 6s cubic-bezier(0.25, 0.45, 0.45, 0.95);transition:all 6s cubic-bezier(0.25, 0.45, 0.45, 0.95)}.menu-item:hover .content{opacity:1}.menu-item.large{height:40vh}.menu-item:first-child{margin-right:7.5px}.menu-item:last-child{margin-left:7.5px}.menu-item .background-image{width:100%;height:100%;background-position:center;background-size:cover}.menu-item .content{text-transform:uppercase;height:90px;padding:0 25px;display:flex;flex-direction:column;align-items:center;justify-content:center;border:1px solid black;background:rgba(255,255,255,0.63);opacity:0.7;position:absolute;-webkit-transition:all .3s ease;transition:all .3s ease}.menu-item .content .title{font-weight:bold;margin-bottom:6px;font-size:22px;color:#4a4a4a}.menu-item .content .subtitle{font-weight:lighter;font-size:16px}

.directory-menu{width:100%;display:flex;flex-wrap:wrap;justify-content:space-between}

.collection-preview{display:flex;flex-direction:column;margin-bottom:1vmax;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;margin-left:auto;margin-right:auto}.collection-preview .title{font-size:28px;margin-bottom:25px}.collection-preview .preview{display:grid;grid-template-columns:1fr 1fr 1fr 1fr 1fr}@media screen and (min-height: 740px){.preview{display:grid;grid-template-columns:1fr 1fr 1fr}}

.custom-button{min-width:165px;width:auto;height:50px;letter-spacing:0.5px;line-height:50px;padding:0 35px 0 35px;font-size:15px;background-color:black;color:white;text-transform:uppercase;font-family:'Open Sans Condensed';font-weight:bolder;cursor:pointer;display:flex;justify-content:center;-webkit-transition:.1s all ease-in-out;transition:.1s all ease-in-out;border:none}.custom-button:hover{background-color:white;color:black}.custom-button.google-sign-in{background-color:#4285f4;color:#fff}.custom-button.google-sign-in:hover{background-color:white;color:black}.custom-button.inverted{background-color:white;color:black}.custom-button.inverted:hover{background-color:black;color:white}

.collection-item{width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;display:block;padding:0;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;align-items:center;padding:.5vmax;-webkit-transition:all ease 1s;transition:all ease 1s;position:relative}.collection-item .image{-webkit-transition:all ease .5s;transition:all ease .5s;background:no-repeat center;width:12vmax;height:15vmax;margin-bottom:1vh}.collection-item .custom-button{position:absolute;width:80%;opacity:.7;top:65%;left:10%;display:none;-webkit-transition:all ease .4s;transition:all ease .4s;border-radius:.1vmax}.collection-item .custom-button:hover{opacity:1}.collection-item:hover .custom-button{display:block}.collection-item:hover .image{opacity:.8}.collection-item .collection-footer{width:100%;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;display:flex;justify-content:space-between;font-size:1vmax}.collection-item .collection-footer .name{font-family:'Ubuntu', sans-serif;width:90%;margin-bottom:15px}.collection-item .collection-footer .price{font-family:'Lato', sans-serif;font-weight:600;height:1.5vmax}.collection-item:hover{box-shadow:0 0 10px #0000005e;z-index:5}

.shop-page{margin-top:5vmax;width:79vw;margin-left:auto;margin-right:auto}.shop-page h1{font-family:'Ubuntu', sans-serif;color:#000000c0}

.group{position:relative;margin:1vmax 0}.group .form-input{background:none;background-color:white;color:rgba(0,0,0,0.836);font-size:1vmax;padding:.5vmax .5vmax .5vmax .2vmax;display:block;font-weight:bold;width:100%;border:none;border-radius:0;border-bottom:1px solid rgba(0,0,0,0.836);margin:25px 0;height:2.5vmax}@media screen and (max-width: 600px){.group .form-input{height:5vh}}.group .form-input:focus{outline:none}.group .form-input:focus ~ .form-input-label{top:-1vmax;font-size:.8vmax;color:grey}.group input[type='password']{letter-spacing:0.3em}.group .form-input-label{color:rgba(0,0,0,0.836);font-size:1vmax;font-weight:bold;position:absolute;pointer-events:none;left:5px;top:10px;-webkit-transition:300ms ease all;transition:300ms ease all}.group .form-input-label.shrink{top:-1vmax;font-size:.8vmax;color:grey}

body{background:#fff}.sign-in,.sign-up{width:20vw;display:flex;flex-direction:column}@media screen and (max-width: 600px){.sign-in,.sign-up{width:90vw}}.sign-in .title,.sign-up .title{margin-top:1vmax}.group{position:relative;margin:1vmax 0}.group .form-input{background:none;background-color:white;color:rgba(0,0,0,0.836);font-size:1vmax;padding:.5vmax .5vmax .5vmax .2vmax;display:block;width:100%;border:none;border-radius:0;border-bottom:1px solid rgba(0,0,0,0.836);margin:25px 0;height:2.5vmax}@media screen and (max-width: 600px){.group .form-input{height:5vh}}.group .form-input:focus{outline:none}.group .form-input.focused{top:-1vmax;font-size:.8vmax;color:grey}.group .form-input:focus ~ .form-input-label{top:-1vmax;font-size:.8vmax;color:grey}.group input[type='password']{letter-spacing:0.3em}.group .form-input-label{color:rgba(0,0,0,0.836);font-size:1vmax;font-weight:normal;position:absolute;pointer-events:none;left:5px;top:10px;-webkit-transition:300ms ease all;transition:300ms ease all}.group .form-input-label.shrink{top:-1vmax;font-size:.8vmax;color:grey}.buttons{display:flex;justify-content:space-between}


.loginPage{display:flex;justify-content:space-between;width:70vw;margin:auto}

.cart-icon{width:45px;height:45px;position:relative;display:flex;align-items:center;justify-content:center;cursor:pointer;-webkit-transition:.3s all ease;transition:.3s all ease}.cart-icon .shopping-icon{width:24px;height:24px}.cart-icon .item-count{position:absolute;font-size:10px;font-weight:bold;bottom:12px}

.cart-item{width:100%;display:flex;height:80px;margin-bottom:15px}.cart-item img{width:30%}.cart-item .item-details{width:70%;display:flex;flex-direction:column;align-items:flex-start;justify-content:center;padding:10px 20px}.cart-item .item-details .name{font-size:16px}

.cart-dropdown{position:absolute;width:240px;height:340px;display:flex;flex-direction:column;padding:20px;border:1px solid black;background-color:white;top:90px;right:40px;z-index:5;-webkit-transition:.3s all ease;transition:.3s all ease}.cart-dropdown .cart-items{height:240px;display:flex;flex-direction:column;overflow:scroll}.cart-dropdown button{margin-top:auto}

header{background:rgba(255,255,255,0.884);height:5vmax;display:flex;align-items:center;padding-left:10vw;padding-right:10vw;position:fixed;top:0;width:100vw;z-index:10;box-shadow:0 0 15px #00000094;justify-content:space-between}header .logo{font-family:'Ubuntu', sans-serif;text-decoration:none;color:#444444;font-size:3vmax}header .option{cursor:pointer}header .title{display:flex}header .title p{color:#333;font-family:'Ubuntu', sans-serif;font-weight:500;font-size:.9vmax;text-transform:uppercase;margin-top:auto;margin-bottom:.1vmax}header .navigation-menu ul{display:flex;list-style-type:none}header li{margin-right:1vw;font-weight:300;font-size:1.1vmax;align-items:center;display:flex}header li a{text-decoration:none;color:#333;font-weight:400}header li a:hover{color:#000}#header-space{min-height:5vmax;visibility:hidden}

